.section {
  background: #fff;

  &__wrap {
    position: relative;
    margin-right: auto;
    margin-left: auto;
    max-width: 1280px;
    height: 100%;
    text-align: center;
  }
  &__container {
    max-width: 1090px;
    margin-left: auto;
    margin-right: auto;
  }
  &__inner {
    position: relative;
    height: 100%;
  }
  &__title {
    margin: 16px auto 10px;
    max-width: 520px;

    b {
      text-transform: lowercase;
      font-weight: 400;
    }
  }
  &__subtitle {

  }
  &__list {
    margin-top: 0;
    padding-left: 18px;
    li {
      text-align: left;
      font-size: 18px;
      color: #3a3a3a;
      letter-spacing: 0.025em;
      font-family: 'PFDinTextPro-Thin';
    }
  }
  &__text {
    margin: 0;
    color: #3a3a3a;
    letter-spacing: 0.025em;
    font-family: 'PFDinTextPro-Thin';
    &_size {
      &_s {
        font-size: 18px;
      }
      &_m {
        font-size: 20px;
        line-height: 29px;
      }
    }
    &_left {
      text-align: left;
    }
    &_center {
      text-align: center;
    }
    &_right {
      text-align: right;
    }

    &_complex {
      margin: 0 auto;
      max-width: 1184px;
    }
    &_info {
      color: #3a3a3a;
      margin: 0 auto;
      max-width: 1100px;
    }
  }
  &__divider {
    display: block;
    margin-right: auto;
    margin-left: auto;
    width: 100px;
    height: 4px;
    background: #ffc300;

    &_blue {
      background: $blue;
    }
    &_white {
      background: #fff;
    }
  }
  &_apartment {
    padding-bottom: 25px;
  }
  &_info {
    padding-bottom: 90px;
  }
  &_communication {
    overflow: hidden;
  }
  &_apartment,
  &_communication,
  &_info {
    position: relative;

    &::before {
      position: absolute;
      bottom: 0;
      width: 100%;
      height: 130px;
      background-image: url('../img/patterns/pattern.png');
      background-position-y: 100%;
      background-repeat: repeat-x;
      content: '';
    }
  }
  &_deals {
    overflow: hidden;
    padding-bottom: 26px;
    width: 100%;
    background: url('../img/pages/home/bg-2.jpg');
    background-position: center center;
    background-size: cover;
    background-repeat: no-repeat;
    box-shadow: 0px 6px 24px 0px rgba(0, 0, 0, 0.25);
  }
  &_dashed {
    position: relative;
    &::before {
      content: '';
      position: absolute;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
      background: linear-gradient(rgba(0, 0, 0, 0.63), rgba(0, 0, 0, 0.63)), url('../img/patterns/dashed.png');
      background-position: center center;
    }
  }
  &_complex {
    padding-bottom: 48px;
  }
  &_about-project {
    overflow: hidden;
    padding-top: 28px;
  }
  &_infrastructure {
    position: relative;
    overflow: hidden;
    padding-bottom: 26px;
    width: 100%;
  }
  &__blocks {
    overflow: hidden;
    background: linear-gradient(rgba(0, 0, 0, 0.63), rgba(0, 0, 0, 0.63)), url('../img/patterns/dashed.png'), url('../img/pages/project/bg-1.jpg');
    background-position: center center;
    background-size: cover;
    background-repeat: no-repeat;
  }
  &__map {
    display: none;
    width: 100%;
    height: 760px;
    background-image: url('../img/pages/project/map.jpg');
    background-position: center center;
    background-size: cover;
    background-repeat: no-repeat;
  }
  &_documentation {
    overflow: hidden;
    padding-bottom: 37px;
    width: 100%;
    background: linear-gradient(rgba(0, 0, 0, 0.67), rgba(0, 0, 0, 0.67)), url('../img/patterns/dashed.png'), url('../img/pages/project/bg-2.jpg');
    background-position: center center;
    background-size: cover;
    background-repeat: no-repeat;
  }
  &_album {
    padding-bottom: 20px;
  }
  &__results-table {
    max-width: 1090px;
    margin-left: auto;
    margin-right: auto;
  }
  &__terms {
    margin-top: 15px;
    text-align: left;
    font-family: 'PFDinTextPro-Thin';
    font-size: 14px;
    .asterisk {
      vertical-align: super;
    }
  }
  &_payment {
    padding-bottom: 28px;
  }
  &_content {
    padding-bottom: 50px;
  }
  &__content {
    @extend %clearfix;
  }
}
